import { DownloadService } from './../../../services/download/download.service';
import { AuthenticationService } from './../../../services/authentication/authentication.service';
import {
	Component,
	OnInit,
	ElementRef
} from '@angular/core';
import { Router } from '@angular/router';
import { ROUTES } from '../sidebar/sidebar.component';
import { Location, LocationStrategy, PathLocationStrategy} from '@angular/common';

@Component({
	// tslint:disable-next-line:component-selector
	selector: 'mviwo-navbar',
	templateUrl: './navbar.component.html',
	styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
	private listTitles: any[];
	public mobile_menu_visible: any = 0;
	private toggleButton: any;
	private sidebarVisible = false;
	private $layer: any;

	constructor(
		public location: Location,
		private element: ElementRef,
		private router: Router,
		private auth: AuthenticationService,
		private download: DownloadService,
	) {}

	ngOnInit() {
		this.listTitles = ROUTES.filter(listTitle => listTitle);
		const navbar: HTMLElement = this.element.nativeElement;
		this.toggleButton = navbar.getElementsByClassName('navbar-toggler')[0];

		this.router.events.subscribe(event => {
			this.sidebarClose();

			this.$layer = document.getElementsByClassName('close-layer')[0];
			if (this.$layer) {
				this.$layer.remove();
				this.mobile_menu_visible = 0;
			}
		});
	}

	sidebarOpen() {
		const body = document.getElementsByTagName('body')[0];

		setTimeout(() => {
			this.toggleButton.classList.add('toggled');
		}, 500);

		body.classList.add('nav-open');
		this.sidebarVisible = true;
	}

	sidebarClose() {
		const body = document.getElementsByTagName('body')[0];
		this.toggleButton.classList.remove('toggled');
		this.sidebarVisible = false;
		body.classList.remove('nav-open');
	}

	sidebarToggle() {
		const $toggle = document.getElementsByClassName('navbar-toggler')[0];

		if (!this.sidebarVisible) {
			this.sidebarOpen();
		} else {
			this.sidebarClose();
		}

		const body = document.getElementsByTagName('body')[0];

		if (this.mobile_menu_visible === 1) {
			body.classList.remove('nav-open');

			if (this.$layer) {
				this.$layer.remove();
			}

			setTimeout(() => {
				$toggle.classList.remove('toggled');
			}, 400);

			this.mobile_menu_visible = 0;
		} else {
			setTimeout(() => {
				$toggle.classList.add('toggled');
			}, 430);

			this.$layer = document.createElement('div');
			this.$layer.setAttribute('class', 'close-layer');

			if (body.querySelectorAll('.main-panel')) {
				document.getElementsByClassName('main-panel')[0].appendChild(this.$layer);
			} else if (body.classList.contains('off-canvas-sidebar')) {
				document.getElementsByClassName('wrapper-full-page')[0].appendChild(this.$layer);
			}

			setTimeout(() => {
				this.$layer.classList.add('visible');
			}, 100);

			this.$layer.onclick = function() {
				body.classList.remove('nav-open');
				this.mobile_menu_visible = 0;
				this.$layer.classList.remove('visible');
				setTimeout(() => {
					this.$layer.remove();
					$toggle.classList.remove('toggled');
				}, 400);
			}.bind(this);

			body.classList.add('nav-open');
			this.mobile_menu_visible = 1;
		}
	}

	getTitle() {
		let title = this.location.prepareExternalUrl(this.location.path());

		if (title.charAt(0) === '#') {
			title = title.slice(2);
		}

		// title = title.split('/').pop();

		for (let i = 0; i < this.listTitles.length; i++) {
			if (this.listTitles[i].path === title ||
				this.listTitles[i].path === title.split('/')[1]) {
				return this.listTitles[i].title;
			}
		}

		return '';
	}

	logout() {
		this.auth.logout();
	}

	downloadToken(): void {
		this.download.asJSON({ token: this.auth.getToken() }, `token_${this.auth.getUserDetails()._id}`);
	}
}
